@use "global" as *;

.p-goods {
  padding-top: rem(75);
  padding-bottom: rem(70);
  background: url(../images/common/bg-goods.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.p-goods__content {
}
.p-goods__title {
  font-size: rem(20);
  color: #ffef00;
  background-color: $red;
  margin: 0 calc(50% - 50vw);
  width: 100vw;
  padding: rem(12) 0;
  text-align: center;
  font-weight: 400;
  @include mq(md) {
    font-size: rem(34);
  }
}
.p-goods__img {
  margin-top: rem(32);
  @include mq(md) {
    margin-top: rem(40);
  }
}
